<script>
import { isValidDate } from './utils.js';

let options = [
    { id: 1, text: 'One-way Flight'},
    { id: 2, text: 'Return Flight'},
];
const ONE_WAY_FLIGHT = options[0];
const RETURN_FLIGHT = options[1];
let selected = ONE_WAY_FLIGHT;

let dateGoing = null;
let dateReturn = null;

// function initErrors() {
//     return {
//         invalidGoingDate: "",
//         invalidReturnDate: "",
//         goingAfterReturn: "",
//     }
// }
// let errors = initErrors();

// errors
$: invalidGoingDate = !isValidDate(dateGoing);
$: invalidReturnDate = selected === RETURN_FLIGHT && !isValidDate(dateReturn);
$: goingAfterReturn = isValidDate(dateGoing) && selected === RETURN_FLIGHT && isValidDate(dateReturn) && (dateGoing.getTime() >= dateReturn.getTime());
$: isError = invalidGoingDate || invalidReturnDate || goingAfterReturn;

// const validateForm = (evt) => {
//     errors = initErrors();

//     // I think error handling can still improve to be more declarative.
    
//     if (!isValidDate(dateGoing)) {
//         errors.invalidGoingDate = "Invalid going date";
//     }

//     if (selected === RETURN_FLIGHT) {
//         if (!isValidDate(dateReturn)) {
//             errors.invalidReturnDate = "Invalid return date";
//         }
        
//         if (isValidDate(dateGoing) && isValidDate(dateReturn)) {
//             const t1 = dateGoing.getTime();
//             const t2 = dateReturn.getTime();
//             if (t1 >= t2) {
//                 errors.goingAfterReturn = "Going date after return date";
//             }
//         }
//     }
// };


</script>


<div class="container">
    
    <div>
        <!-- on:change={validateForm} on:blur={()=>{}} -->
        <select bind:value={selected} >
            {#each options as option}
                <option value={option}>
                    {option.text}
                </option>
            {/each}
        </select>
    </div>

    <div>
        <input type="date"
            on:input={(evt) => {
                dateGoing = new Date(evt.target.value);
                
            }}
            class:error={invalidGoingDate || goingAfterReturn}
        />
    </div>
    
    <div>
        <input type="date"
            on:input={(evt) => {
                dateReturn = new Date(evt.target.value);
            }}
            disabled={selected === ONE_WAY_FLIGHT}
            class:error={invalidReturnDate || goingAfterReturn}
            />
    </div>

    <div>
        <button disabled={isError}>Book</button>
    </div>
    
    {#if invalidGoingDate}
        <div class="error-message">
            error: invalid going date.
        </div>
    {/if}

    {#if invalidReturnDate}
        <div class="error-message">
            error: invalid return date.
        </div>
    {/if}

    {#if goingAfterReturn}
        <div class="error-message">
            error: going after return.
        </div>
    {/if}
    
</div>

<style>
.container {
    width: 196px;
    margin: 0 auto;
}
select, button, input[type="date"] {
    width: 100%;
    display: block;
}
input.error {
    color: red;
    font-weight: bold;
}
.error-message {
    color: red;
    font-weight: bold;
    font-size: 12px;
}
</style>