<div dir="ltr" class="snippet">
	<div class="snippet-name">
		{name}
	</div>
	<pre>
		<code class="lang-{lang}" bind:this={self}>
			{code}
		</code>
	</pre>
	{#if !code}
		<div>waiting <b>{src}</b> to be fetched</div>
