<script>
import KEYCODE from "./KeyCode.js";
import { ALPHABET } from "./Constants.js";
import Table from "./Table";

const theTable = new Table();
const table = theTable.store;

let editedCell = null; // being edited
let selectedCell = null; // clicked once

const handleCellClick = (cell) => (evt) => {
    console.log("cell", cell);
};

const handleCellDoubleClick = (cell) => (evt) => {
    editedCell = cell;
    setTimeout(() => {
        document.getElementById(cell.id).focus();
    }, 0);
};

const handleKey = (cell) => (evt) => {
    if (KEYCODE.ENTER === evt.keyCode) {
        evt.preventDefault();
        console.log("evt.target.innerHTML =", evt.target.innerHTML);
        editedCell.eval(evt.target.innerHTML);
        editedCell = null;
    }
};

const handleCellBlur = (cell) => (evt) => {
    if (editedCell) {
        // cell.expr = cell.expr;
        // updateTable()
        editedCell = null;
    }
};
</script>

<!-- <svelte:window on:keydown={handleKey}/> -->

<div class="container">

    <div class="table">
        <div class="row">
            {#each ['', ...ALPHABET.split('')] as letter}
                <div class="cell">{letter}</div>
            {/each}
        </div>

        {#each $table as row, i}
            <div class="row">
                <div class="cell">{i}</div>
                {#each row as cell}
                    {#if cell === editedCell}
                        <div
                            id="{cell.id}"
                            class="cell"
                            contenteditable="true"
                            on:keypress="{handleKey(cell)}"
                            on:blur="{handleCellBlur(cell)}">
                            {cell.expr}
                        </div>
                    {:else}
                        <div
                            id="{cell.id}"
                            class="cell"
                            on:keypress="{handleKey(cell)}"
                            on:blur="{handleCellBlur(cell)}"
                            on:click="{handleCellClick(cell)}"
                            on:dblclick="{handleCellDoubleClick(cell)}"
                            class:type-text="{cell.vType === 'Text'}"
                            class:type-number="{cell.vType === 'Number'}"
                            class:type-expression="{cell.vType === 'Expression'}"
                            class:type-identifier="{cell.vType === 'Identifier'}">
                            {cell.text}
                        </div>
                    {/if}
                {/each}
            </div>
        {/each}
    </div>

    <!-- Color Keys -->
    <div class="color-keys">
        <span class="type-text">Text</span>
        <span class="type-number">Number</span>
        <span class="type-expression">Expression</span>
        <span class="type-identifier">Identifier</span>
    </div>
</div>

<style>
.container {
    width: 480px;
    margin: 0 auto;
}

.table {
    /* display: table; */
    width: 100%;
    height: 196px;
    user-select: none;
    overflow: auto;
    white-space: nowrap;
}

.row {
    /* display: table-row; */
    height: 1.4em;
    user-select: none;
}

.cell {
    /* display: table-cell; */
    width: 90px;
    height: 100%;
    display: inline-block;
    user-select: none;
    border: 1px solid #eee;
    vertical-align: bottom;
}

.type-text {
    color: #333;
}

.type-number {
    color: limegreen;
}

.type-expression {
    color: blueviolet;
}

.type-identifier {
    color: chocolate;
}
</style>
