<script>
import {onMount} from 'svelte';
import ContextMenu from "../shared/ContextMenu.svelte";
import Modal from "../shared/Modal.svelte";
import { UndoStack } from './undo.js'

let isMenuOpen = false;
let isModalOpen = false;

let idCounter = 1;
let circles = [];
let selectedCircle = null;

// svg
let svgdom;

// mouse position in the page for context menu
let mouseX = 0;
let mouseY = 0;

// undo stack
let undoStack = UndoStack(circles);

const leftClick_Space = (evt) => {
  if (isMenuOpen) {
    isMenuOpen = false;
  } else {
    drawCircle(evt.offsetX, evt.offsetY, 40);
  }

  if (isModalOpen) {
    isModalOpen = false;
  }

  if (selectedCircle) {
    selectedCircle = null;
  }
};

const drawCircle = (x, y, r=40) => {
  const circle = {
    id: idCounter++,
    cx: x,
    cy: y,
    r:  r,
    stroke: 'black',
    strokeWidth: 2,
    fill: "white",
    // fill-opacity="0.5"
  };
  circles = [...circles, circle];
  // update undo stack
  undoStack.push([...circles]);
};

const leftClick_Circle = (evt) => {
  console.log(evt.target)
};

const rightClick_Circle = (evt) => {
  // Open Context Menu
  isMenuOpen = true;
  mouseX = evt.pageX;
  mouseY = evt.pageY;

  // select circle
  selectedCircle = circles.find(c => Number(evt.target.id) === c.id);
}

const leftClick_Adjust = (evt) => {
  isModalOpen = true;
};

const leftClick_ContextMenuItem = (evt) => {
  isMenuOpen = false;
};

const handleRadiusChange = (evt) => {
  if (!selectedCircle) {
    return;
  }
  // update radius
  let circle = {...selectedCircle};
  circle.r = evt.target.value;

  // update circles list
  circles = circles.map(c => {
    if (c.id === circle.id) {
      return circle;
    }
    return c;
  })
  // @TODO: Update the undo stack when the dialoge closes, not on every slider tick.
  // update undo stack
  undoStack.push([...circles]);
};

const undo = () => {
  circles = [...undoStack.undo()];
};

const redo = () => {
  circles = [...undoStack.redo()];
};

const leftClick_ModalBackdrop = () => {
  isModalOpen = false;
  if (selectedCircle) {
    selectedCircle = null;
  }
};

const rightClick_ModalBackdrop = () => {
  isModalOpen = false;
  if (selectedCircle) {
    selectedCircle = null;
  }
};

</script>


<ContextMenu open={isMenuOpen} x={mouseX} y={mouseY} on:click={leftClick_ContextMenuItem}>
  <div on:click={leftClick_Adjust}>Adjust</div>
  <div>Option 2</div>
  <div>Option 3</div>
</ContextMenu>

<div class="container">
  
  <div class="top">
    <button on:click={undo}>Undo</button>
    <button on:click={redo}>Redo</button>
  </div>
  
  <div class="box">
    
    <Modal show={isModalOpen} on:click={leftClick_ModalBackdrop} on:contextmenu={rightClick_ModalBackdrop} >
      <div>
        Adjust diameter of circle at: ({selectedCircle.cx}, {selectedCircle.cy}).
      </div>
      <div>
        <input type="range" min="16" max="96" step="8" on:input={handleRadiusChange}>
      </div>
    </Modal>

    <svg bind:this={svgdom} on:click={leftClick_Space}>
      {#each circles as c (c.id)}
        <circle cx={c.cx} cy={c.cy} r={c.r} stroke={c.stroke} stroke-width={c.strokeWidth} fill={c.fill}
          on:contextmenu|preventDefault|self={rightClick_Circle}
          on:click|self|stopPropagation={leftClick_Circle}
          id={`${c.id}`}
          class:selected={selectedCircle && selectedCircle.id === c.id}
          />
      {/each}
    </svg>
  </div>
</div>


<style>
.container {
  width: 480px;
  margin: 0 auto;
}

.box {
  position: relative;
}

svg {
  width: 100%;
  height: 256px;
  border: 1px solid #eee;
}

.top {
  text-align: center;
}

circle.selected {
  fill: gray;
}
</style>