<script lang="ts">export let open = false;
export let x = 0;
export let y = 0;
let menu;
$: if (open) {
    if (menu) {
        menu.style.left = `${x}px`;
        menu.style.top = `${y}px`;
        menu.style.display = "block";
    }
}
else {
    if (menu) {
        menu.style.display = "none";
    }
}
//# sourceMappingURL=ContextMenu.svelte.js.map</script>

<div class="menu" bind:this="{menu}" on:click>
    <div class="menu-options">
        <slot />
    </div>
</div>

