<script>
const BOX_HEIGHT = 6;

let idCounter = 4;
let people = [
  { id: 1, firstName: "Emil", surname: "Hans" },
  { id: 2, firstName: "Mustermann", surname: "Max" },
  { id: 3, firstName: "Tisch", surname: "Roman" }
];

// fields
let rawFirstName = "";
let rawSurname = "";
$: firstName = rawFirstName.trim();
$: surname = rawSurname.trim();
// filter
let rawPrefix = "";
$: prefix = rawPrefix.trim();
$: peopleFiltered = people.filter(p =>
  p.surname.toLocaleLowerCase().startsWith(prefix.toLocaleLowerCase())
);
// selection
let selectedPerson = null;

// methods
const handleCreate = () => {
  const person = {
    id: idCounter++,
    firstName,
    surname
  };
  people = [...people, person];
};

const handleUpdate = () => {
  people = people.map(p => {
    if (p.id === selectedPerson.id) {
      return {
        id: p.id,
        firstName: firstName ? firstName : selectedPerson.firstName,
        surname: surname ? surname : selectedPerson.surname
      };
    }
    return p;
  });
};

const handleDelete = () => {
  people = people.filter(p => p.id !== selectedPerson.id);
  selectedPerson = null;
};
</script>

<div class="grid-container">

  <div class="topLeft">
    <label for="prefix">
      Filter prefix
      <small>(surname)</small>
      :
    </label>
    <input type="text" id="prefix" bind:value={rawPrefix} />
  </div>

  <div class="middleLeft">
    <select bind:value={selectedPerson} size={BOX_HEIGHT}>
      {#each peopleFiltered as p (p.id)}
        <option value={p}>{p.firstName}, {p.surname}</option>
      {/each}
    </select>
  </div>

  <div class="middleRight">
    <form autocomplete="off">
      <div>
        <label for="name">Name:</label>
        <input type="text" id="name" bind:value={rawFirstName} />
      </div>

      <div>
        <label for="surname">Surname:</label>
        <input type="text" id="surname" bind:value={rawSurname} />
      </div>
    </form>
  </div>

  <div class="topRight">
    <!-- Empty -->
  </div>

  <div class="bottom">
    <button on:click={handleCreate}>Create</button>
    <button
      on:click={handleUpdate}
      disabled={!selectedPerson || !(firstName || surname)}>
      Update
    </button>
    <button on:click={handleDelete} disabled={!selectedPerson}>Delete</button>
  </div>
</div>


<style>
/* .container {
      width: 320px;
      margin: 0 auto;
      text-align: left;
  } */

select {
  width: 100%;
}

label {
  display: block;
}

.grid-container {
  text-align: left;

  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 2fr 5fr 2fr;
  grid-template-areas:
    "topLeft      topRight"
    "middleLeft middleRight"
    "bottom          bottom";
  grid-column-gap: 8px;
}

.topLeft {
  grid-area: topLeft;
}

.middleLeft {
  grid-area: middleLeft;
}

.middleRight {
  grid-area: middleRight;
}

.topRight {
  grid-area: topRight;
}

.bottom {
  grid-area: bottom;
}
</style>