<script>
export let show = false;
</script>

{#if show}
<div class="backdrop" on:click|self on:contextmenu|preventDefault|self>
    <div class="modal">
        <slot></slot>
    </div>
</div>
{/if}

<style>
/* https://www.w3schools.com/css/css_positioning.asp */
.backdrop {
    width: 100%;
    height: 100%;
    position: absolute;
    background: rgba(0, 0, 0, 0.4);
}

.modal {
    padding: 10px;
    border-radius: 10px;
    max-width: 400px;
    margin: 10% auto;
    text-align: center;
    background: white;
}
</style>